import React from "react";
import Select from 'react-select';
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CTextarea,
  CLabel,
  CSelect,
  CInput,
  CRow,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from 'react-places-autocomplete';
import {
  get_buisnsess_data,
  edit_Business,
  Viewbuisness_data_byid,
} from "../../../redux/allbuisness/action";
import { connect } from "react-redux";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { toast } from "react-toastify";

const options = [
  { value: '0', label: '$' },
  { value: '1', label: '$$' },
  { value: '2', label: '$$$' },
  ];
class Editbuisness extends React.Component {
  constructor(props) {
    //this.handleSelect= this.handleSelect.bind(this);
    //this.handleChange=this.handleChange.bind(this);
    super(props);

    this.state = {
      data: {},
      address:'',
      latitude:'',
      longitude:'',
      selectedOption: null,
    };
    this.formValidate = this.formValidate.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handlesChange = selectedOption => {
    this.setState({ selectedOption });
   
    };
  handleChanges = address => {
    this.setState({ address });
  };
  handleSelects = address => {
    geocodeByAddress(address)
      .then(results => getLatLng(results[0]))
      .then(latLng => {
        this.state.latitude = latLng.lat;
        this.state.longitude = latLng.lng;
        this.state.address=address
      
        document.getElementById("email").focus();
      })
      .catch(error => console.error('Error', error));
  };
  componentDidMount = async () => {
    await this.props.Viewbuisness_data_byid(this.props.match.params.clientId);
    let selectedClient = this.props.allbuisness.view_buisness_id.business;

 
    await this.setState({
      data: selectedClient,
      id: this.props.match.params.clientId,
    });
  };

  formValidate = () => {
    let { data, error } = this.state;
    let fieldList = ["name", "email", "phone"];
    let is_valid = true;
    for (let x of fieldList) {
      if (!data[x]) {
        is_valid = false;
        this.setState((prevState) => ({
          error: { ...prevState.error, [x]: "Please fill in the above field" },
        }));
      }
    }
    return is_valid;
  };

  handleChange(event) {
    event.preventDefault();
    let data = this.state.data;
    let name = event.target.name;
    let value = event.target.value;

    data[name] = value;

    this.setState({ data });
  }

  handleSubmit(event) {
    event.preventDefault();

    const { data, id } = this.state;
    if(this.state.selectedOption!=null){
      data.value=this.state.selectedOption.value
    }
    if (!this.formValidate()) {
      return;
    }
    const payload = {
      id: data.id,
      name: data.name,
      phone: data.phone,
      email:data.email,
      address:this.state.address,
      price_category:data.price_category,
      opening_day:data.opening_day,
      closing_day:data.closing_day,
      opening_hours:data.opening_hours,
      closing_hours:data.closing_hours,
      business_id:data.id,
      latitude:this.state.latitude,
      price_category:data.value,
      longitude:this.state.longitude,
      twitter_link:data.twitter_link,
      fb_link:data.fb_link,
      ig_link:data.ig_link,
      web_link:data.web_link,
      services_description:data.services_description

    };
    // const sendData = JSON.stringify(payload);
   
    this.props
      .edit_Business(payload)
      .then(async () => {
        this.props.history.push("/allbuisness");
        this.setState({
          isLogin: false,
        });
      })
      .catch((err) => {
        console.log("err", err);
      });
  }
  render() {
    
    const { data, errors } = this.state;

    return (
      <>
        <CCard>
          <CCardHeader>Edit Buisness</CCardHeader>
          <CCardBody>
            <CForm
              action=""
              method="post"
              encType="multipart/form-data"
              className="form-horizontal"
            >
              <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">Name</CLabel>
                </CCol>
                <CCol xs="12" md="9">
                  <CInput
                    name="name"
                    id="name"
                    onChange={this.handleChange.bind(this)}
                    value={data.name}
                    placeholder="Enter name"
                  />
                </CCol>
              </CFormGroup>
              <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">Email</CLabel>
                </CCol>
                <CCol xs="12" md="9">
                  <CInput
                    name="email"
                    id="email"
                     onChange={this.handleChange.bind(this)}
                     ref={this.myRef}
                    value={data.email}
                    placeholder="Enter email"
                  />
                </CCol>
              </CFormGroup>
              <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">Phone</CLabel>
                </CCol>
                <CCol xs="12" md="9">
                  <CInput
                    name="phone"
                    id="phone"
                    onChange={this.handleChange.bind(this)}
                    value={data.phone}
                    placeholder="Enter phone"
                  />
                </CCol>
              </CFormGroup>
              <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">Address</CLabel>
                </CCol>
                <CCol xs="12" md="9">
               
                  
                  <PlacesAutocomplete
                       
                       value={this.state.address}
                       onChange={this.handleChanges.bind(this)}
                       onSelect={this.handleSelects.bind(this)}
    >
      {({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
        <div>
       <CInput
          style={{width:'100%'}}
            {...getInputProps({
              placeholder: data.address,
              className: 'location-search-input',
            })}
            value={this.state.address}
          />
          <div className="autocomplete-dropdown-container">
            {loading && <div>Loading...</div>}
            {suggestions.map(suggestion => {
              const className = suggestion.active
                ? 'suggestion-item--active'
                : 'suggestion-item';
              // inline style for demonstration purpose
              const style = suggestion.active
                ? { backgroundColor: '#fafafa', cursor: 'pointer' }
                : { backgroundColor: '#ffffff', cursor: 'pointer' };
              return (
                <div
                  {...getSuggestionItemProps(suggestion, {
                    className,
                    style,
                  })}
                >
                  <span>{suggestion.description}</span>
                </div>
              );
            })}
          </div>
        </div>
      )}
    </PlacesAutocomplete>

    </CCol>
    <CCol xs="12" md="6">

 
            
              
            <CInput 
              name="long" 
             type="hidden"
              id="latitude" 
              rows="9"
              value={this.state.latitude}
               onChange={this.onChange}
             
    
            />
    </CCol>
    <CCol xs="12" md="6">
 
               <CInput 
              name="longitude" 
             type="hidden"
              id="longitude" 
              rows="9"
              value={this.state.latitude}
               onChange={this.onChange}
             
          
            />
      </CCol>

          

               
              </CFormGroup>
              <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">Price category</CLabel>
                </CCol>
                <CCol xs="12" md="9">
                                    <Select
                    value={this.state.selectedOption}
                    onChange={this.handlesChange.bind(this)}
                    options={options}
                   placeholder={data.price_category === 0 ? '$' : (data.price_category === 1 ? '$$' : (data.price_category === 2 ? '$$$' : '') )}
                    />
                                    </CCol>
              </CFormGroup>
              <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">Opening day</CLabel>
                </CCol>
                <CCol xs="12" md="9">
                  <CSelect
                    name="opening_day"
                    id="opening_day"
                    onChange={this.handleChange.bind(this)}
                    value={data.opening_day}
                    placeholder="Opening day"
                  ><option >Select the days </option>
                    <option >Monday </option>
                  <option >Tuesday</option>
                  <option >Wednesday </option>
                  <option >Thrusday</option>
                  <option >Friday</option>
                  <option >Saturday</option>
                  <option >Sunday</option></CSelect>
                </CCol>
              </CFormGroup>
              <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">closing day</CLabel>
                </CCol>
                <CCol xs="12" md="9">
                  <CSelect
                    name="closing_day"
                    id="closing_day"
                    onChange={this.handleChange.bind(this)}
                    value={data.closing_day}
                    placeholder="closing_day"
                  ><option >Select the days </option>
                    <option >Monday </option>
                  <option >Tuesday</option>
                  <option >Wednesday </option>
                  <option >Thrusday</option>
                  <option >Friday</option>
                  <option >Saturday</option>
                  <option >Sunday</option></CSelect>
                </CCol>
              </CFormGroup>
              <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">Opening hours</CLabel>
                </CCol>
                <CCol xs="12" md="9">
                  <CInput
                    name="opening_hours"
                    type="time"
                    id="opening_hours"
                    onChange={this.handleChange.bind(this)}
                    value={data.opening_hours}
                    placeholder="Opening hours"
                  />
                </CCol>
              </CFormGroup>
              <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">Closing hours</CLabel>
                </CCol>
                <CCol xs="12" md="9">
                  <CInput
                    name="closing_hours"
                    type="time"
                    id="closing_hours"
                    onChange={this.handleChange.bind(this)}
                    value={data.closing_hours}
                    placeholder="Closing hours"
                  />
                </CCol>
              </CFormGroup>
              <CRow>
                 <CCol xs="3">
                 <CLabel htmlFor="textarea-input">Social Media</CLabel>
                 </CCol>
               <CCol xs="3">
                <CFormGroup >
               
                <CInput 
                       name="fb_link"
                       id="fb_link"
                       onChange={this.handleChange.bind(this)}
                       value={data.fb_link}
                       placeholder="Facebook" 
                    />
                {/* {errors.opening_day ? (
                <CLabel className="is-invalid text-danger">
                {errors.opening_day}
                </CLabel>
                ) : (
                ""

                )} */}

     
                </CFormGroup>
                </CCol>
                <CCol xs="3">
                <CFormGroup >

               
                <CInput 
                       name="ig_link"
                       id="ig_link"
                       onChange={this.handleChange.bind(this)}
                       value={data.ig_link}
                     
                     
                      placeholder="Instagram" 
                    />
                {/* {errors.opening_day ? (
                <CLabel className="is-invalid text-danger">
                {errors.opening_day}
                </CLabel>
                ) : (
                ""

                )} */}

     
                </CFormGroup>
                </CCol>
                <CCol xs="3">
                <CFormGroup >
           

                <CInput 
                      name="twitter_link" 
                
                      id="twitter_link" 

  
                       onChange={this.handleChange.bind(this)}
                       value={data.twitter_link}
                     
                     
                
                     
                      placeholder="Twitter" 
                    />
                {/* {errors.opening_day ? (
                <CLabel className="is-invalid text-danger">
                {errors.opening_day}
                </CLabel>
                ) : (
                ""

                )} */}

     
                </CFormGroup>
                </CCol>
               </CRow>
               <CFormGroup row>
                <CCol md="3">
                  <CLabel htmlFor="select">Webpage Link</CLabel>
                </CCol>
                <CCol xs="12" md="9">
                  <CInput
                    name="web_link"
                    id="web_link"
                    value={data.web_link}
                     onChange={this.handleChange.bind(this)}
                
                    value={data.web_link}
                    placeholder="Webpage Link"
                  />
                </CCol>
              </CFormGroup>
              <CFormGroup  className="description"  xs="12" md="9" row>
                  <CCol md="3">
                    <CLabel htmlFor="textarea-input">Services description</CLabel>
                  </CCol>
               
                  <CCol>
                    <CTextarea 
                
                name="services_description"
                id="services_description"
                value={data.service_description}
                 onChange={this.handleChange.bind(this)}
            
                value={data.services_description}
            
                    />
           
                  </CCol>
                </CFormGroup>
            </CForm>
          </CCardBody>
          <CCardFooter>
            <CButton
              type="submit"
              onClick={this.handleSubmit}
              size="sm"
              color="primary"
            >
              <CIcon name="cil-scrubber" /> Submit
            </CButton>
          </CCardFooter>
        </CCard>
        <ToastContainer hideProgressBar={true} />


      </>
    );
  }
}
const mapStateToProps = (state) => ({
  ...state,
});
export default connect(mapStateToProps, {
  get_buisnsess_data,
  Viewbuisness_data_byid,
  edit_Business,
})(Editbuisness);
